<template>
    <el-table
        border
        :data="tableData"
        style="width: 98%">
        <el-table-column
          label=""
          prop="id"
          width="50">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          >
        </el-table-column>
        <el-table-column
          label=""
          width="250"
          >
          <template slot-scope="scope">
            <el-button size="mini" type="success" round @click="editFn(scope.row)" v-show="templateUse"></el-button>
            <el-button size="mini" type="primary" round @click="downloadFn(scope.row)"></el-button>
            <el-button size="mini" type="danger" round @click="deleteFn(scope.row)" v-show="templateUse"></el-button>
          </template>
        </el-table-column>
      </el-table>
</template>
<script>
export default {
    name:'tableTemplate',
    props:[
        'tableData',
        'templateUse'//
    ],
    methods:{
        editFn(val){
            this.$emit('editFn',val)
        },
        downloadFn(val){
            this.$emit('downloadFn',val)
        },
        deleteFn(val){
            this.$emit('deleteFn',val)
        }
    }
}
</script>




