<script>
import '@/assets/css/system.css'
import addImport from '@/functions/editDialog/addArea.vue'
import pageChange from '@/components/pageChange.vue'
import  { axiosRequest } from '@/assets/js/Yt.js'
import { Message } from 'element-ui'
// import indexMethod  from '@/utils/indexMethod.js'

export default {
    components:{
        'edit-dialog':addImport,
        'page-change':pageChange
    },
    data() {
        return {
			totalNum:'',//
			titalAmount:'',//
			formKey:{
				key:''
			},
			form: {
          		name: '',
				region: ''
			},
			formInline: {
				user: '',
				region: ''
			},
            id:'',//
            page:1,
            page_size:10,
            total:0,
            bannerTitle:"",
            addNow:false,
            tableData: [{
                miniImage: ''
            }, {
                miniImage: ''
            }],
            formTitle:{//
                name:''
            },
        }
    },
    created() {
        this.init()
    },
    methods: {
		handleSelectionChange(val){
			console.log(val)
		},
		searchFn(){

		},
        onSubmit(){

		},
        init(){
            let conf = {
                url : '/api/api_backend.php?r=system-setting/area-list',
                data : {
                    page:this.page,
                    page_size:this.page_size
                },
                success:(data)=>{
                    if( data.statusCode == 1 ){
                        this.tableData = data.info
                        this.formTitle
                        this.total = Number( data.total )
                    }
                }
            }
            axiosRequest(conf)
        },
        addFn(val){//
            this.bannerTitle = ""
            this.addNow = val
            this.id = ''
        },
        changeId(){//
            this.id = ''
        },
        clearFormTitle(){//
            this.formTitle = {
                name:''
            }
        },
        editFn(row){//
            this.bannerTitle = ""
            this.id = row.id
            this.addNow = true
            this.formTitle = {
                name:row.name
            }
        },
        pageSizeChangeFn(val){
            // console.log(val)
            this.page_size = val
            this.init()
        },
        currentPageChangeFn(val){
            this.page = val
            this.init()
        },
        deleteFn(row){
             this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let conf = {
                    url : '/api/api_backend.php?r=system-setting/area-del',
                    data : {
                        id:row.id
                    },
                    success:(data)=>{
                        if( data.statusCode == 1 ){
                            this.init()
                            Message({
                                message: data.message,
                                type: 'success',
                                duration: 3 * 1000
                            })
                        }else if(data.statusCode == 0){
                            Message({
                                message: data.message,
                                type: 'erro',
                                duration: 3 * 1000
                            })
                        }
                    }
                }
                axiosRequest(conf)
            }).catch( () =>{
                Message({
                    message:'',
                    type: 'erro',
                    duration: 3 * 1000
                })
            })
        }
    }
}
</script>
