<template>
     <el-menu
        default-active="1" :unique-opened="open" @open="handleOpen"
          @close="handleClose"
          @select="handleSelect" router
        class="el-menu-vertical-demo" style="height:100%" background-color="#333744" text-color="#fff"  active-text-color="#ffd04b" >
          <el-menu-item :index="item.path" v-for="(item,index) in data" v-if="item.child.length==0" :key="index">
            <i :class="item.icon"></i>
            <span slot="title">{{item.name}}</span>
          </el-menu-item>
          <el-submenu :index="item.id" v-for="(item,index) in data" v-if="item.child.length!=0" :key="index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span>{{item.name}}</span>
            </template>
            <el-menu-item-group>
              <el-menu-item :index="item.path" v-for="(item,index) in item.child" :key="index">{{item.name}}</el-menu-item>
            </el-menu-item-group>
          </el-submenu> 
          <!--<el-menu-item index="/caseManagement">
            <i class="fa fa-home"></i>
            <span slot="title"></span>
          </el-menu-item> 
          <el-menu-item index="/dataManagement">
            <i class="fa fa-home"></i>
            <span slot="title"></span>
          </el-menu-item> 
          <el-menu-item index="/smsManagement">
            <i class="fa fa-home"></i>
            <span slot="title"></span>
          </el-menu-item> 
          <el-menu-item index="/speechScheme">
            <i class="fa fa-home"></i>
            <span slot="title"></span>
          </el-menu-item> 
           <el-menu-item index="/privilegeManagement">
            <i class="fa fa-home"></i>
            <span slot="title"></span>
          </el-menu-item> 
          <el-submenu index="2">
            <template slot="title">
              <i class="fa fa-chain"></i>
              <span></span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/regionalConfiguration"></el-menu-item>
              <el-menu-item index="/clientConfiguration" ></el-menu-item>
              <el-menu-item index="/caseConfiguration"></el-menu-item>
              <el-menu-item index="/accountManagement"></el-menu-item>
              <el-menu-item index="/templateManagement"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>  -->    
      </el-menu>       
</template>
<script type="text/ecmascript-6">
import store from '../vuex/store.js'
  export default {
    data(){
      return {
        open:true,
        data:[
          {
            id:'1',
            name:'',
            path:'/taskManagement',
            icon:'fa fa-tasks',
            child:[]
          },
          {
            id:'2',
            name:'',
            path:'/caseManagement',
            icon:'fa fa-address-card-o',
            child:[]
          },
           {
            id:'3',
            name:'',
            path:'/dataStatistics',
            icon:'fa fa-bar-chart-o ',
            child:[]
          },
          
           {
            id:'4',
            name:'',
            path:'/dataManagement',
            icon:'fa fa-file-text-o',
            child:[]
          },
           {
            id:'5',
            name:'',
            path:'',
            icon:'fa fa-envelope-o',
            child:[{
              name:"",
              path:"/smsManagement"
            },{
              name:"",
              path:"/smsRecord"
            }]
          },
           {
            id:'6',
            name:'',
            path:'',
            icon:'fa fa-file-sound-o',
            child:[{
              name:"",
              path:"/speechScheme"
            },{
              name:"",
              path:"/Learning"
            }]
          },
           {
            id:'7',
            name:'',
            path:'/privilegeManagement',
            icon:'fa fa-sitemap',
            child:[]
          },
          {
            id:'8',
            name:'',
            path:'',
            icon:'fa fa-users',
            child:[{
              name:"",
              path:"/departmentManagement"
            },{
              name:"",
              path:"/collectorManagement"
            }]
          },
           {
            id:'9',
            name:'',
            path:'',
            icon:'fa fa-gear',
            child:[
              {
              name:'',
              path:'/regionalConfiguration'
            },
             {
              name:'',
              path:'/clientConfiguration'
            },
             {
              name:'',
              path:'/caseConfiguration'
            },
             {
              name:'',
              path:'/accountManagement'
            },
             {
              name:'',
              path:'/templateManagement'
            },
            {
              name:'',
              path:'/levelSetting'
            }
            ]
          },
        ]
      }
    },
 mounted () {
        //console.log(this)
        if(this.$route.name != ""){
          if(this.$route.path !== '/taskManagement' && this.$route.path.indexOf('taskManagement') === -1) {
            store.commit('add_tabs', {route: '/taskManagement', name: ''}); 
           store.commit('add_tabs', {route: this.$route.path , name: this.$route.name });
            store.commit('set_active_index', this.$route.path);
            store.commit('save_index', this.$route.query.num); 
          } else {
            store.commit('add_tabs', {route: '/taskManagement', name: ''});
              store.commit('set_active_index', '/taskManagement');
              this.$router.push('/taskManagement');
          }
        }
        
    },
    
    methods:{
       handleOpen(key, keyPath) {
         console.log(key)
            store.commit('save_index', key);  
        },
        handleClose(key, keyPath) {
            console.log(key,keyPath);
        },
        handleSelect(key,path){
          console.log(key,path)
          store.commit('save_index', path[1]);
        },
      handleIconClick(ev) {
        console.log(ev);
      },
    },
  }
</script>


